body{
  padding: 0px;
  margin: 0px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
a{
  text-decoration: none;
  color: black;
  
}
.navbar{
  align-items: center;
  justify-content: center;
  display: flex;
  /* background-color: bisque; */
  border-bottom: 5px solid;
  border-color: rgb(0, 242, 255);
}
.navbar div{
  margin: 2vh;
  padding: 1vh;
}
.navbar button{
  background-color: aqua;
  cursor: pointer;
  border: none;
  padding: 5px;
  margin: 5px;
  border-radius: 4px;
}
/* .navbar button::hover{

} */
.container{
  justify-content: center;
  align-items: center;
  display: flex;
  flex-wrap: wrap;

}
.cont-data{
  /* border: 2px solid;
  border-color: black; */
  margin: 2vh;
  padding: 2vh;
  width: 30vh;
  height: 40vh;
}
.cont-data img{
  width:20vh;
  height:30vh;
}
.cont-data p{
  margin-top: 0px;
  margin-bottom: 0px;
}
.cont-data small{
  color: rgb(0, 187, 255);
}
.form-container{
  
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px;
  min-height: 300px;	 
}
.form{
  margin-top: 100px;
  background-color: rgb(218, 218, 214);
  border-radius: 5px;
  width: 200px;
  padding: 50px;
  padding-bottom: 90px;
}
.form input{
  border: none;
  border-radius: 5px;
  width: 178px;
  padding: 10px;
}
/* .form-button{
  display: flex;
  align-items: center;
  justify-content: center;
} */
.form-button{
  text-align: center;
  cursor: pointer;
  border:none;
  border-radius: 5px;
  width: 178px;
  padding: 10px;
  color: white;
  background-color: rgb(6, 198, 192);
  display: block;
  /* backcolor: aquamarine; */
}
